<template>
<div>
   <el-table
      :data="memberList"
      style="width: 100%;margin-bottom: 20px;"
      row-key="id"
      border
    >
      <el-table-column prop="uid" label="用户编号"  width="120"></el-table-column>
      <el-table-column prop="nickname" label="昵称"></el-table-column>
     <el-table-column prop="phone" label="手机号"></el-table-column>
      <el-table-column label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status===1">启用</el-button>
          <el-button type="info" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.uid)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
   <v-edit :info='info' ref="add" ></v-edit>
</div>
</template>
<script>
import vEdit from './edit.vue'

import {mapActions,mapGetters} from 'vuex'
export default {
components:{
   vEdit
 },
 computed: {
    ...mapGetters({
       memberList:'member/member'
    })
 },
data () {
 return {
    info:{
       isShow:false,
       title:"会员修改"
    },
 }
},
methods:{
   ...mapActions({
      requsetMemberList:'member/requsetMemberList'
   }),
   edit(uid){
      this.info={
         isShow:true,
         title:'会员修改'
      };
      this.$refs.add.getOne(uid)
   }
},
mounted(){
   this.requsetMemberList()
   
}
}
</script>
<style scoped>
</style>


